<template>
  <main class="noselect screen-centered">
    <div id="stack" :class="{ incomplete: stack.length > 3 }">
      <span v-for="(value, index) in stack_shown" :key="index">
        {{ prettyPrint(value) }}
      </span>
    </div>
    <div id="input">{{ inputfield }}</div>
    <section>
      <div
        v-for="(key, index) in face"
        :key="index"
        :id="'key-' + key.cmd"
        class="button"
        @click="doCommand(key.cmd)"
        :style="{
          'grid-column-start': key.mincol + 1,
          'grid-column-end': key.maxcol + 1,
          'grid-row-start': key.row,
        }"
      >
        {{ key.icon }}
      </div>
    </section>
  </main>
</template>

<script>
import { Decimal } from "decimal.js";

const maxlength = 20;
const face = [
  {
    icon: "0",
    row: 5,
    mincol: 0,
    maxcol: 3,
    cmd: "0",
  },
  {
    icon: ".",
    row: 5,
    mincol: 3,
    maxcol: 6,
    cmd: ".",
  },
  {
    icon: "+",
    row: 5,
    mincol: 9,
    maxcol: 12,
    cmd: "+",
  },
  {
    icon: "1",
    row: 4,
    mincol: 0,
    maxcol: 3,
    cmd: "1",
  },
  {
    icon: "2",
    row: 4,
    mincol: 3,
    maxcol: 6,
    cmd: "2",
  },
  {
    icon: "3",
    row: 4,
    mincol: 6,
    maxcol: 9,
    cmd: "3",
  },
  {
    icon: "-",
    row: 4,
    mincol: 9,
    maxcol: 12,
    cmd: "-",
  },
  {
    icon: "4",
    row: 3,
    mincol: 0,
    maxcol: 3,
    cmd: "4",
  },
  {
    icon: "5",
    row: 3,
    mincol: 3,
    maxcol: 6,
    cmd: "5",
  },
  {
    icon: "6",
    row: 3,
    mincol: 6,
    maxcol: 9,
    cmd: "6",
  },
  {
    icon: "×",
    row: 3,
    mincol: 9,
    maxcol: 12,
    cmd: "*",
  },
  {
    icon: "7",
    row: 2,
    mincol: 0,
    maxcol: 3,
    cmd: "7",
  },
  {
    icon: "8",
    row: 2,
    mincol: 3,
    maxcol: 6,
    cmd: "8",
  },
  {
    icon: "9",
    row: 2,
    mincol: 6,
    maxcol: 9,
    cmd: "9",
  },
  {
    icon: "÷",
    row: 2,
    mincol: 9,
    maxcol: 12,
    cmd: "/",
  },
  {
    icon: "C",
    row: 1,
    mincol: 0,
    maxcol: 3,
    cmd: "Backspace",
  },
  {
    icon: "↺",
    row: 1,
    mincol: 3,
    maxcol: 6,
  },
  {
    icon: "⇵",
    row: 1,
    mincol: 6,
    maxcol: 9,
    cmd: "Tab",
  },
  {
    icon: "↑",
    row: 1,
    mincol: 9,
    maxcol: 12,
    cmd: "Enter",
  },
];

export default {
  data: function () {
    return {
      stack: [],
      inputfield: "",
      face: face,
    };
  },
  mounted() {
    document.addEventListener("keydown", this.keypressed);
    document.addEventListener("keyup", this.keylifted);
  },
  beforeDestroy() {
    document.removeEventListener("keydown", this.keypressed);
    document.removeEventListener("keyup", this.keylifted);
  },
  computed: {
    stack_shown: function () {
      return this.stack.slice(-3);
    },
  },
  methods: {
    doCommand: function (cmd) {
      let value = cmd;
      if (value >= "0" && value <= "9" && this.inputfield.length < maxlength) {
        this.inputfield += value;
      } else if (value == ".") {
        if (
          !isNaN(Number(this.inputfield + ".")) &&
          this.inputfield.length < maxlength
        )
          this.inputfield += ".";
      } else if (value == "Backspace") {
        if (this.inputfield !== "")
          this.inputfield = this.inputfield.slice(0, -1);
        else this.stack.pop();
      } else if (value == "Enter") {
        if (this.inputfield !== "" && !isNaN(Number(this.inputfield))) {
          this.stack.push(new Decimal(this.inputfield));
          this.inputfield = "";
        }
      } else if (value == "Tab") {
        if (this.stack.length > 1) {
          let x = this.stack.pop();
          let y = this.stack.pop();
          this.stack.push(x);
          this.stack.push(y);
        }
      } else if ("+-*/".includes(value)) {
        this.binaryOperation(value);
      } else {
        return;
      }
      return "unknown";
    },
    prettyPrint: function (number) {
      let repr = number.toFixed();
      if (repr.length <= maxlength) return repr;
      repr = number.toPrecision(maxlength - 1);
      if (repr.length > maxlength) repr = number.toExponential(maxlength);
      if (repr.includes("e")) {
        let sp = repr.split("e");
        return number.toExponential(maxlength - 3 - sp[1].length);
      }
      return repr;
    },
    keypressed: function (ev) {
      if (this.doCommand(ev.key) == "unknown") {
        var elem = document.getElementById("key-" + ev.key);
        elem.classList.add("hovered");
        ev.preventDefault();
        ev.stopPropagation();
      }
    },
    keylifted: function (ev) {
      var elem = document.getElementById("key-" + ev.key);
      if (elem !== null) elem.classList.remove("hovered");
      ev.preventDefault();
      ev.stopPropagation();
    },
    pushOrFail: function () {
      if (this.inputfield === "") return true;
      if (!isNaN(Number(this.inputfield))) {
        this.stack.push(new Decimal(this.inputfield));
        this.inputfield = "";
        return true;
      }
      return false;
    },
    binaryOperation: function (key) {
      if (this.pushOrFail() === false) return null;
      if (this.stack.length < 2) return null;
      let x = this.stack.pop();
      let y = this.stack.pop();
      switch (key) {
        case "+":
          this.stack.push(y.plus(x));
          break;
        case "-":
          this.stack.push(y.minus(x));
          break;
        case "*":
          this.stack.push(y.times(x));
          break;
        case "/":
          this.stack.push(y.dividedBy(x));
          break;
      }
    },
  },
};
</script>

<style>
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300&display=swap");
body {
  margin: 0;
}
.screen-centered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}
main > div,
section {
  font-size: 32px;
  width: 21ch;
  line-height: 1.2em;
  text-align: right;
  font-family: "IBM Plex Mono", monospace;
}
div#input {
  height: 1.2em;
  border-top: 2px solid black;
}
div#stack {
  height: 3.6em;
  flex-direction: column;
  display: flex;
  justify-content: flex-end;
}
div#stack span {
  height: 1.2em;
  display: block;
}
div#stack.incomplete::before {
  content: "↑";
}
section {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, 3ch);
}
.bluestripes {
  background-image: linear-gradient(
    45deg,
    #aeceeb 25%,
    #ffffff 25%,
    #ffffff 50%,
    #aeceeb 50%,
    #aeceeb 75%,
    #ffffff 75%,
    #ffffff 100%
  );
  background-size: 33.94px 33.94px;
}
div.button {
  text-align: center;
  background-color: white;
  cursor: pointer;
  line-height: 3ch;
}
div.button:hover {
  background-color: #f7f1d6;
}
div.button.hovered {
  background-color: #f7f1d6;
}
</style>